.messages {
  background-color: #eee;
  position: absolute;
  width: 100%;
  height: 80%;
  margin:0;
  padding: 0;
  top: 10%;
  overflow-x: auto;
  border: solid #eee 2px;
}

.message_header {
  position: absolute;
  height: 10%;
  width: 100%;
  top: 0;
  padding: 0;
  margin:0;
  border-bottom: solid #ccc 2px;
}

.message_footer {
  position: absolute;
  width: 100%;
  height : 10%;
  top : 90%;
  margin:0;
  padding:0;
  border-top: solid #ccc 2px;
}

.draggable {
  /* just to size it to content */
  display: inline-block;
  /* opaque background is important for performance */
  background: #ddd;
  border: solid #ccc 2px;
  border-radius : 7px;
  /* avoid selecting text while dragging */
  user-select: none;
  text-align: left;
  height: 500px;
  width: 400px;
}

.line {
  height : 3px;
  background-color : #ccc;
  margin:0;
  padding: 0;
}

.message-me {
  width: 300px;
  margin-left: 75px;
}

.left-m {
  margin-left: 10px;
  margin-right: 10px;
}
.message-me  > .left-m {
  text-align: right;
}

.right-m {
  max-width: 300px;
  margin: 10px;
  word-wrap: break-word;
  border: solid #aaa 2px;
  border-radius: 5px;
  padding: 5px;
  background-color: #ccc;


}

.carte{
  background-color: rgba(185,000,255, 0.1);
  color: rgb(0,0,0);
  border:thin solid black;
  padding:5px;
  border-radius:10px;
}

.match{
  text-decoration: none;
  color: #fff;
}

.match:hover{
  text-decoration: none;
  color: #fff;
}
.message-me > .right-m {
    background-color: rgba(185, 000, 255, 0.5);
}
